*, *:before, *:after {
    box-sizing: border-box;
    margin: 0px;
    padding: 0;
    user-select: none;
    outline: none;
    -webkit-tap-highlight-color: transparent;
}

@font-face {
    font-family: 'Nunito-Black';
    src: url(/weather/fonts/Nunito-Black.ttf);
}

@font-face {
    font-family: 'Nunito-Bold';
    src: url(/weather/fonts/Nunito-Bold.ttf);
}

@font-face {
    font-family: 'Nunito-Regular';
    src: url(/weather/fonts/Nunito-Regular.ttf);
}

a {
    text-decoration: none;
    color: inherit;
}

img {
    width: inherit;
    height: inherit;
    max-width: 100%;
}

li {
    list-style: none;
}

body {
    /* background: linear-gradient(to top left, #174D5F, #175F5F); */
    background-color: #F7F7F7;
    content-visibility: auto;
}